.tradingContainer {
    background: linear-gradient(135deg, #1a1a1a 0%, #2d2d2d 100%);
    min-height: 100vh;
    color: #ffffff;
    padding: 2rem;
    width: 99%;
}

.header {
    text-align: center;
    margin-bottom: 3rem;
}

.header h1 {
    font-size: 3rem;
    color: #ffd700;
    text-shadow: 0 0 10px rgba(255, 215, 0, 0.3);
    margin-bottom: 0.5rem;
}

.subtitle {
    color: #cccccc;
    font-size: 1.6rem;
}

.marketStats {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 2rem;
    margin-bottom: 3rem;
}

.statCard {
    background: rgba(255, 255, 255, 0.1);
    border-radius: 10px;
    padding: 1.5rem;
    backdrop-filter: blur(5px);
    border: 1px solid rgba(255, 255, 255, 0.1);
}

.statCard h3 {
    color: #cccccc;
    margin-bottom: 1rem;
}

.value {
    font-size: 1.5rem;
    font-weight: bold;
    color: #ffffff;
}

.up {
    color: #00ff88;
}

.tradingFeatures {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 2rem;
}

.featureBox {
    background: rgba(255, 255, 255, 0.05);
    border-radius: 15px;
    padding: 2rem;
    text-align: center;
    transition: transform 0.3s ease;
}

.featureBox:hover {
    transform: translateY(-5px);
}

.featureBox h2 {
    color: #ffd700;
    margin-bottom: 1rem;
    font-size: 1.7rem;
}

.featureBox p {
    color: #cccccc;
    margin-bottom: 1.5rem;
    font-size: 1.6rem;
}

.tradeBtn {
    background: linear-gradient(90deg, #ffd700, #ff8c00);
    border: none;
    padding: 0.8rem 1.5rem;
    border-radius: 25px;
    font-size: 1.7rem;
    color: #1a1a1a;
    font-weight: bold;
    cursor: pointer;
    transition: all 0.3s ease;
}

.tradeBtn:hover {
    transform: scale(1.05);
    box-shadow: 0 0 15px rgba(255, 215, 0, 0.5);
}

@media (max-width: 768px) {
    .tradingContainer {
        padding: 1rem;
    }
    
    .header h1 {
        font-size: 2rem;
    }
    
    .statCard {
        padding: 1rem;
    }
}